<template>
  <div class="wrapper">
    <div class="container">
      <div class="title">区块详情</div>
      <div class="block">
        区块高度：
        <span>{{height}}</span>
        <!-- <span class="copy-wrapper">
          <img src="../../assets/index_img/copy.svg" alt />
        </span>-->
        <span class="img-content">
          <img
            @click="toleft(-1)"
            :class="{'none':noneafter}"
            src="../../assets/index_img/left-arrow.svg"
            alt
          />
          <img
            @click="toleft(1)"
            :class="{'none':nonenext}"
            src="../../assets/index_img/right-arrow.svg"
            alt
          />
        </span>
      </div>
      <div class="detai-content">
        <div class="item-content">
          <el-tooltip content="生成时间" :visible-arrow="false" placement="top-start" effect="light">
            <div class="title-content">生成时间</div>
          </el-tooltip>
          <div class="title-text">{{time}}</div>
        </div>
        <div class="item-content">
          <div class="title-content">哈希值</div>
          <div class="title-text">{{sales}}</div>
        </div>
        <div class="item-content">
          <div class="title-content">高度</div>
          <div class="title-text">{{height}}</div>
        </div>
        <div class="item-content">
          <div class="title-content">交易</div>
          <div class="title-text">{{salenum}}</div>
        </div>
        <!-- <div class="item-content">
          <div class="title-content">共识数据</div>
          <div class="title-text">7658391230024700933</div>
        </div>-->
      </div>
    </div>
  </div>
</template>

<script>
import { getblockdetail } from "../../../api";
import moment from "moment/moment";
export default {
  mounted() {
    this.unit = sessionStorage.getItem("unit")
      ? sessionStorage.getItem("unit")
      : "MCN";
    var id = this.$route.query;
    getblockdetail({ height: id.id })
      .then((res) => {
        // this.id = res.data.data[0].id;
        this.height = res.data.data[0].height;
        this.time = moment(res.data.data[0].timestamp.human).format(
          "YYYY-MM-DD HH:mm:ss"
        );
        this.bid = res.data.data[0].id;
        this.handling = res.data.data[0].forged.fee;
        // this.id = res.data.data[0].id;
        this.sales = res.data.data[0].payload.hash;
        this.salenum = res.data.data[0].transactions;
      })
      .catch((err) => {
        window.console.log(err);
      });
  },
  data() {
    return {
      dealList: [],
      h: 0,
      id: 0,
      height: 0,
      sales: 0,
      all: 0,
      time: 0,
      saleAll: 0,
      handling: 0,
      pre: 0,
      next: 0,
      acN: false,
      acP: false,
      notice: false,
      allnum: 0,
      nextId: 0,
      preId: 0,
      bid: 0,
      unit: "",
      salenum: 0,
      noneafter: false,
      nonenext: false,
    };
  },
  methods: {
    toleft(n) {
      getblockdetail({ height: this.height + n })
        .then((res) => {
          console.log(res.data);
          if (res.data.data.length == 0 && n == 1) {
            this.nonenext = true;
            return false;
          } else {
            this.nonenext = false;
          }
          if (res.data.data.length == 0 && n == -1) {
            this.noneafter = true;
            return false;
          } else {
            this.noneafter = false;
          }
          // this.id = res.data.data[0].id;
          this.height = res.data.data[0].height;
          this.time = moment(res.data.data[0].timestamp.human).format(
            "YYYY-MM-DD HH:mm:ss"
          );
          this.bid = res.data.data[0].id;
          this.handling = res.data.data[0].forged.fee;
          // this.id = res.data.data[0].id;
          this.sales = res.data.data[0].payload.hash;
          this.salenum = res.data.data[0].transactions;
        })
        .catch((err) => {
          window.console.log(err);
        });
    },
  },
};
</script>

